<template>
  <el-form
    :inline="true"
    size="small"
    :model="formInline"
    class="demo-form-inline"
  >
    <slot name="optionsType"></slot>
    <CommonStatusSelect
      v-model="status"
      :moduleName="moduleName"
    ></CommonStatusSelect>
    <el-form-item>
      <el-button type="primary" @click="onSubmit">搜索</el-button>
      <el-button type="primary" @click="reset">重置</el-button>
    </el-form-item>
  </el-form>
</template>
<script>
import CommonStatusSelect from '@/components/crm/SelectComponents/CommonStatusSelect'

export default {
  data() {
    return {
      formInline: {},
      status: ''
    }
  },
  props: {
    optionsVal: [Array, String],
    moduleName: {
      type: String,
      default: ''
    }
  },
  components: {
    CommonStatusSelect
  },
  methods: {
    onSubmit() {
      const searchData = {
        opration_action: this.optionsVal,
        is_open: this.status
      }
      this.$emit('searchFun', searchData)
    },
    reset() {
      this.status = ''
      this.$emit('reset')
    }
  }
}
</script>
