<template>
  <div>
    <SearchForm
      :optionsVal="actionValue"
      @reset="resetForm"
      @searchFun="searchFun"
    >
      <OpratorActionSelect
        slot="optionsType"
        :moduleName="moduleName"
        :isOpen="'all'"
        v-model="actionValue"
      ></OpratorActionSelect>
    </SearchForm>
    <el-button
      type="primary"
      size="small"
      @click="addOpratorAction"
      class="margin-bottom-30"
      >新增{{ moduleName }}</el-button
    >
    <MyTable
      size="mini"
      :stripe="false"
      :border="true"
      :multiple="false"
      :operation="operation"
      :column="column"
      :data="tableData"
      :currentPage="currentPage"
      :pageSize="pageSize"
      :total="total"
      @update="update"
      @actionTableSwitchChange="handleStatusColClick"
      @handleSizeChange="handleSizeChange"
      @handleCurrentChange="handleCurrentChange"
    ></MyTable>
    <Dialog
      v-model="dialogVisible"
      :title="dialogTitle"
      :width="dialogType !== 'status' ? '30%' : '20%'"
      :hasConfirmBtn="dialogType === 'status'"
      :showCancelBtn="dialogType === 'status'"
      @cancel="getActionList"
      @btnHandle="statusConfirm"
    >
      <div slot="form">
        <MyForm
          v-if="dialogType !== 'status'"
          :form="dialogForm"
          ref="operatorActionAddForm"
          :formData="formData"
          :formItem="formItem"
          @submit="dialogSubmit"
        ></MyForm>
      </div>
      <div v-if="dialogType === 'status'" slot="content">
        <p>{{ dialogContent }}</p>
      </div>
    </Dialog>
  </div>
</template>
<script>
import OpratorActionSelect from '@/components/crm/SelectComponents/OpratorActionSelect.vue'
import { getField, getFormField } from '../getField'
import SearchForm from '../components/SearchForm'
import MyTable from '@/components/MyTable'
import {
  getOpratorActionlList,
  addOpratorAction as addOpratorActionApi,
  putOpratorAction as putOpratorActionApi
} from '@/api/crmSetting'
import Dialog from '@/components/Dialog'
import MyForm from '@/components/MyForm'

export default {
  data() {
    // 表单配置
    var dialogForm = {
      showFormBorder: false,
      showTitle: false,
      ref: 'operatorActionAddForm',
      labelWidth: '100px',
      labelPositon: 'right',
      submitDisabled: false,
      style: 'notprepend',
      column: 1,
      hasSubmit: true,
      submitText: '确认新增',
      cancleText: '取消'
    }
    // 表格操作配置
    var operation = {
      nowPage: 'actionTable',
      show: true,
      fixed: false,
      size: 'mini',
      width: 80,
      minWidth: 100,
      label: '操作',
      btns: [
        {
          type: 'text',
          size: 'mini',
          content: '编辑',
          icon: '',
          handle: 'update',
          class: 'button-operator'
        }
      ]
    }
    return {
      dialogType: 'status',
      moduleName: '运营动作',
      dialogContent: '确认开启该运营动作吗？',
      titleOprator: '新增',
      dialogTitle: '',
      dialogVisible: false,
      dialogForm: dialogForm,
      actionValue: '',
      searchParams: {},
      formItem: [],
      formData: {},
      column: [],
      putVal: {
        is_open: false,
        _id: ''
      },
      tableData: [],
      pageSize: 10,
      currentPage: 1,
      total: 0,
      operation: operation // 操作按钮
    }
  },
  components: {
    MyForm,
    SearchForm,
    Dialog,
    OpratorActionSelect,
    MyTable
  },
  created() {
    this.getActionList()
    this.fieldInit()
  },
  methods: {
    handleStatusColClick({ row, $index }) {
      this.dialogTitle = '状态切换提示'
      this.dialogContent = `确认${row.status ? '开启' : '关闭'}该客户类型吗？`
      this.dialogForm.submitText = '状态切换'
      this.dialogType = 'status'
      this.putVal.is_open = row.status
      this.putVal._id = row._id
      this.dialogShow()
    },
    setTableData({ list }) {
      this.tableData = list.map((item, index) => ({
        _id: item._id ? item._id : index,
        id: item.id ? item.id : '',
        name: item.name ? item.name : '-',
        desc: item.desc ? item.desc : '-',
        creatorName:
          item.creator && item.creator.name ? item.creator.name : '-',
        createAt: item.created_at ? this.formatTime(item.created_at) : '-',
        status: item.is_open ? item.is_open : false
      }))
    },
    getActionList() {
      this.handleClose()
      const pageAttr = {
        page_size: this.pageSize,
        current_page: this.currentPage
      }
      const params = Object.assign({}, pageAttr, this.searchParams)
      getOpratorActionlList(params).then(res => {
        if (res) {
          this.total = res.data.count
          this.setTableData(res.data)
        }
      })
    },
    fieldInit() {
      // 获取字段
      var column = getField('opratorAction')
      column.forEach(item => {
        if (!!item.width && item.width != 'auto') {
          item.width = '300'
        }
      })
      this.column = column
    },
    // 更新数据
    update(row) {
      this.setDialogAttr('编辑', 'update', '确认修改')
      this.dialogFormInit(row)
      this.formItem[0].disabled = true
      this.formItem[2].show = false
      this.dialogShow()
    },
    setDialogAttr(oprator, dialogType, submitText) {
      this.titleOprator = oprator
      this.dialogTitle = this.titleOprator + this.moduleName
      this.dialogType = dialogType
      this.dialogForm.submitText = submitText
    },
    handleSizeChange(val) {
      this.pageSize = val
      this.getActionList()
    },
    // 分页切换
    handleCurrentChange(index) {
      this.currentPage = index
      this.getActionList()
    },
    // 搜索
    searchFun(val) {
      this.searchParams = val
      this.currentPage = 1
      this.getActionList()
    },
    resetForm() {
      this.actionValue = ''
      this.searchParams = {}
      this.currentPage = 1
      this.getActionList()
    },
    // 弹窗相关操作
    addOpratorAction() {
      this.setDialogAttr('新增', 'add', '确认新增')
      const data = this.initFormData()
      this.dialogFormInit(data)
      this.dialogShow()
    },
    dialogShow() {
      this.dialogVisible = true
    },
    handleClose() {
      this.dialogVisible = false
    },
    initFormData() {
      const data = {
        name: '',
        desc: ''
      }
      return data
    },
    dialogFormInit(row) {
      this.formItem = getFormField('opratorAction', 'item')
      this.formData = row ? row : getFormField('opratorAction', 'data')
      this.formItem[0].disabled = false
      this.formItem[2].show = true
    },
    dialogSubmit() {
      this.dialogForm.submitDisabled = true
      if (this.formData._id) {
        this.putOpratorAction(this.formData, '')
        return
      }
      addOpratorActionApi(this.formData)
        .then(res => {
          this.dialogForm.submitDisabled = false
          this.$message({
            type: res.data && res.data.result ? 'success' : 'error',
            message:
              res.data && res.data.result
                ? this.dialogForm.submitText + '成功'
                : res.data.message
          })
          if (res && res.data.result) {
            this.getActionList()
          }
        })
        .catch(error => {
          this.dialogForm.submitDisabled = false
        })
    },
    statusConfirm() {
      this.putOpratorAction(this.putVal, 'status')
    },
    putOpratorAction(val, type) {
      putOpratorActionApi(val, type)
        .then(res => {
          this.dialogForm.submitDisabled = false
          this.$message({
            type: res.data && res.data.result ? 'success' : 'error',
            message:
              res.data && res.data.result
                ? this.dialogForm.submitText + '成功'
                : this.dialogForm.submitText + '失败'
          })
          if (res && res.data.result) {
            this.getActionList()
          }
        })
        .catch(error => {
          this.dialogForm.submitDisabled = false
        })
    }
  }
}
</script>
