<template>
  <div>
    <el-tabs v-model="activeName" @tab-click="handleClick">
      <el-tab-pane label="客户类型" name="customerType"></el-tab-pane>
      <el-tab-pane label="来源渠道" name="chanel"></el-tab-pane>
      <el-tab-pane label="运营动作" name="action"></el-tab-pane>
    </el-tabs>
    <div>
      <userType v-if="activeName === 'customerType'"></userType>
      <opratorAction v-else-if="activeName === 'action'"></opratorAction>
      <sourceChannel v-else></sourceChannel>
    </div>
  </div>
</template>

<script>
import userType from './userType'
import opratorAction from './opratorAction'
import sourceChannel from './sourceChannel'

export default {
  name: 'index',
  data() {
    return {
      activeName: 'customerType'
    }
  },
  components: {
    userType,
    opratorAction,
    sourceChannel
  },
  created() {
    this.getLocalActiveName()
  },
  methods: {
    getLocalActiveName() {
      if (window.sessionStorage.getItem('crm_activeName') != null) {
        this.activeName = window.sessionStorage.getItem('crm_activeName')
          ? window.sessionStorage.getItem('crm_activeName')
          : 'customerType'
      }
    },
    handleClick() {
      window.sessionStorage.setItem('crm_activeName', this.activeName)
    }
  }
}
</script>
