<template>
  <!-- crm模块，通用状态select,开启：true,关闭：false -->
  <el-form-item label="状态" :prop="prop" :rules="rules">
    <el-select
      :value="value"
      :placeholder="'请选择' + moduleName + '状态'"
      @change="onValueChange"
      clearable
      :disabled="disabled"
    >
      <el-option
        v-for="item in statusOptions"
        :key="item.value"
        :value="item.value"
        :label="item.label"
        >{{ item.label }}</el-option
      >
    </el-select>
  </el-form-item>
</template>
<script>
export default {
  data() {
    return {
      statusOptions: [
        {
          label: '开启',
          value: true
        },
        {
          label: '关闭',
          value: false
        }
      ]
    }
  },
  props: {
    value: [String, Boolean],
    moduleName: {
      type: String,
      default: ''
    },
    prop: {
      type: String,
      default: ''
    },
    rules: {
      type: Array,
      default: () => []
    },
    disabled: {
      type: Boolean,
      default: false
    }
  },
  methods: {
    onValueChange(val) {
      this.$emit('input', val)
    }
  }
}
</script>
