<template>
  <div>
    <SearchForm
      :optionsVal="channelVlaue"
      @reset="resetForm"
      @searchFun="searchFun"
    >
      <SourceChannelSelect
        slot="optionsType"
        :moduleName="moduleName"
        v-model="channelVlaue"
        :isOpen="'all'"
        @thirdValue="thirdValue"
      ></SourceChannelSelect>
    </SearchForm>
    <el-button
      type="primary"
      size="small"
      @click="addSourceChannel"
      class="margin-bottom-30"
      >新增{{ moduleName }}</el-button
    >
    <MyTable
      size="mini"
      :stripe="false"
      :border="true"
      :multiple="false"
      :operation="operation"
      :column="column"
      :data="tableData"
      :currentPage="currentPage"
      :pageSize="pageSize"
      :total="total"
      @update="update"
      @channelTableSwitchChange="handleStatusColClick"
      @handleSizeChange="handleSizeChange"
      @handleCurrentChange="handleCurrentChange"
    ></MyTable>
    <Dialog
      v-model="dialogVisible"
      :title="dialogTitle"
      :width="dialogType !== 'status' ? '30%' : '20%'"
      :hasConfirmBtn="dialogType === 'status'"
      :showCancelBtn="dialogType === 'status'"
      @cancel="getSourceChannelList"
      @btnHandle="statusConfirm"
    >
      <div slot="form">
        <MyForm
          v-if="dialogType !== 'status'"
          :form="dialogForm"
          ref="operatorActionAddForm"
          :formData="formData"
          :formItem="formItem"
          @submit="dialogSubmit"
        ></MyForm>
      </div>
      <div v-if="dialogType === 'status'" slot="content">
        <p class="margin-bottom-10">{{ dialogContent }}</p>
      </div>
    </Dialog>
  </div>
</template>
<script>
import SourceChannelSelect from '@/components/crm/SelectComponents/SourceChannelSelect.vue'
import { getField, getFormField } from '../getField'
import SearchForm from '../components/SearchForm'
import MyTable from '@/components/MyTable'
import {
  getSourceChannelList,
  addSourceChannel as addSourceChannelApi,
  putSourceChannel as putSourceChannelApi
} from '@/api/crmSetting'
import Dialog from '@/components/Dialog'
import MyForm from '@/components/MyForm'
import { mapGetters } from 'vuex'
import _ from 'lodash'
export default {
  data() {
    // 表单配置
    var dialogForm = {
      showFormBorder: false,
      showTitle: false,
      ref: 'operatorActionAddForm',
      labelWidth: '120px',
      labelPositon: 'right',
      submitDisabled: false,
      style: 'notprepend',
      column: 1,
      hasSubmit: true,
      submitText: '确认新增',
      cancleText: '取消'
    }
    // 表格操作配置
    var operation = {
      nowPage: 'channelTable',
      show: true,
      fixed: false,
      size: 'mini',
      width: 80,
      minWidth: 100,
      label: '操作',
      btns: [
        {
          type: 'text',
          size: 'mini',
          content: '编辑',
          icon: '',
          handle: 'update',
          class: 'button-operator'
        }
      ]
    }
    return {
      dialogType: 'status',
      moduleName: '来源渠道',
      dialogContent: '确认开启该来源渠道吗？',
      titleOprator: '新增',
      dialogTitle: '',
      dialogVisible: false,
      dialogForm: dialogForm,
      channelVlaue: '',
      searchParams: {},
      formItem: [],
      formData: {},
      column: [],
      putVal: {
        is_open: false,
        _id: ''
      },
      searchFlag: false,
      thirds: [],
      tableData: [],
      pageSize: 10,
      currentPage: 1,
      total: 0,
      operation: operation // 操作按钮
    }
  },
  computed: {
    ...mapGetters([
      'oneChannelOptions',
      'twoChannelOptions',
      'threeChannelOptions',
      'sourceChannelOptions'
    ])
  },
  components: {
    MyForm,
    SearchForm,
    Dialog,
    SourceChannelSelect,
    MyTable
  },
  created() {
    this.fieldInit()
    this.getSourceChannelList()
  },
  watch: {
    'formData.first': {
      handler(val) {
        if (val && this.dialogType !== 'update') {
          this.formData.second = ''
          const selectItem = _.find(this.sourceChannelOptions, function(o) {
            return o.value == val
          })
          this.formItem[1].options = selectItem.children
        }
      },
      deep: true
    }
  },
  methods: {
    initFormData() {
      const data = {
        first: '',
        second: '',
        third: '',
        desc: ''
      }
      return data
    },
    handleStatusColClick({ row, $index }) {
      this.dialogTitle = '状态切换提示'
      this.dialogType = 'status'
      this.dialogForm.submitText = '状态切换'
      this.dialogContent = `确认${row.status ? '开启' : '关闭'}该来源渠道吗？`
      this.putVal.is_open = row.status
      this.putVal._id = row._id
      this.dialogShow()
    },
    setTableData({ list }) {
      this.tableData = list.map((item, index) => ({
        _id: item._id ? item._id : index,
        id: item.id ? item.id : '',
        first: item.first ? item.first : '-',
        second: item.second ? item.second : '-',
        third: item.third ? item.third : '',
        desc: item.desc ? item.desc : '-',
        creator: item.creator ? item.creator : '-',
        creatorName:
          item.creator && item.creator.name ? item.creator.name : '-',
        createAt: item.created_at ? this.formatTime(item.created_at) : '-',
        status: item.is_open ? item.is_open : false
      }))
    },
    getSourceChannelList() {
      this.handleClose()
      const pageAttr = {
        page_size: this.pageSize,
        current_page: this.currentPage
      }
      const params = Object.assign({}, pageAttr, this.searchParams)
      if (this.searchFlag) {
        params.thirds = this.thirds
      }
      getSourceChannelList(params).then(res => {
        this.searchFlag = false
        if (res) {
          this.total = res.data.count
          this.setTableData(res.data)
        }
      })
    },
    fieldInit() {
      // 获取字段
      var column = getField('sourceChannel')
      column.forEach(item => {
        if (!!item.width && item.width != 'auto') {
          item.width = '300'
        }
      })
      this.column = column
    },
    // 更新数据
    update(row) {
      this.setDialogAttr('编辑', 'update', '确认修改')
      this.dialogFormInit(row)
      this.formItem[0].disabled = true
      this.formItem[1].disabled = true
      this.formItem[2].disabled = true
      this.formItem[4].show = false
      this.dialogShow()
    },
    setDialogAttr(oprator, dialogType, submitText) {
      this.titleOprator = oprator
      this.dialogTitle = this.titleOprator + this.moduleName
      this.dialogType = dialogType
      this.dialogForm.submitText = submitText
    },
    handleSizeChange(val) {
      this.pageSize = val
      this.getSourceChannelList()
    },
    // 分页切换
    handleCurrentChange(index) {
      this.currentPage = index
      this.getSourceChannelList()
    },
    thirdValue(val) {
      this.thirds = val
    },
    // 搜索
    searchFun(val) {
      this.searchParams.is_open = String(val.is_open)
      this.currentPage = 1
      this.searchFlag = true
      this.getSourceChannelList()
    },
    resetForm() {
      this.channelVlaue = ''
      this.searchParams = {}
      this.currentPage = 1
      this.getSourceChannelList()
    },
    // 弹窗相关操作
    addSourceChannel() {
      this.setDialogAttr('新增', 'add', '确认新增')
      const data = this.initFormData()
      this.dialogFormInit(data)
      this.dialogShow()
    },
    dialogShow() {
      this.dialogVisible = true
    },
    handleClose() {
      this.dialogVisible = false
    },
    dialogFormInit(row) {
      this.formItem = getFormField('sourceChannel', 'item')
      this.formItem[0].options = this.oneChannelOptions
      this.formItem[0].disabled = false
      this.formItem[1].disabled = false
      this.formItem[2].disabled = false
      this.formItem[4].show = true
      if (this.dialogType === 'update') {
        this.formItem[1].options = this.twoChannelOptions
      } else {
        this.formItem[1].options = []
      }
      this.formData = row ? row : getFormField('sourceChannel', 'data')
    },
    dialogSubmit() {
      this.dialogForm.submitDisabled = true
      if (this.formData._id) {
        this.putSourceChannel(this.formData, '')
        return
      }
      addSourceChannelApi(this.formData)
        .then(res => {
          this.dialogForm.submitDisabled = false
          this.$message({
            type: res.data && res.data.result ? 'success' : 'error',
            message:
              res.data && res.data.result
                ? this.dialogForm.submitText + '成功'
                : res.data.message
          })
          if (res && res.data.result) {
            this.getSourceChannelList()
            this.$store.dispatch('crm/getChannelOptions', '', {
              root: true
            })
          }
        })
        .catch(error => {
          this.dialogForm.submitDisabled = false
        })
    },
    statusConfirm() {
      this.putSourceChannel(this.putVal, 'status')
    },
    putSourceChannel(val, type) {
      putSourceChannelApi(val, type).then(res => {
        this.dialogForm.submitDisabled = false
        this.$message({
          type: res.data && res.data.result ? 'success' : 'error',
          message:
            res.data && res.data.result
              ? this.dialogForm.submitText + '成功'
              : this.dialogForm.submitText + '失败'
        })
        if (res && res.data.result) {
          this.getSourceChannelList()
        }
      })
    }
  }
}
</script>
