<template>
  <div>
    <SearchForm
      :optionsVal="customerTypeValue"
      @reset="resetForm"
      @searchFun="searchFun"
    >
      <userTypeSelect
        :moduleName="moduleName"
        slot="optionsType"
        :isOpen="'all'"
        v-model="customerTypeValue"
      ></userTypeSelect>
    </SearchForm>
    <el-button
      type="primary"
      size="small"
      @click="addUserType"
      class="margin-bottom-30"
      >新增{{ moduleName }}</el-button
    >
    <MyTable
      size="mini"
      :stripe="false"
      :border="true"
      :multiple="false"
      :operation="operation"
      :column="column"
      :data="tableData"
      :currentPage="currentPage"
      :pageSize="pageSize"
      :total="total"
      @update="update"
      @customerTableSwitchChange="handleStatusColClick"
      @handleSizeChange="handleSizeChange"
      @handleCurrentChange="handleCurrentChange"
    ></MyTable>
    <Dialog
      v-model="dialogVisible"
      :title="dialogTitle"
      :width="dialogType !== 'status' ? '30%' : '20%'"
      :hasConfirmBtn="dialogType === 'status'"
      :showCancelBtn="dialogType === 'status'"
      @cancel="getUserTypeList"
      @btnHandle="statusConfirm"
    >
      <div slot="form">
        <MyForm
          v-if="dialogType !== 'status'"
          :form="dialogForm"
          ref="operatorActionAddForm"
          :formData="formData"
          :formItem="formItem"
          @submit="dialogSubmit"
        ></MyForm>
      </div>
      <div v-if="dialogType === 'status'" slot="content">
        <p>{{ dialogContent }}</p>
      </div>
    </Dialog>
  </div>
</template>
<script>
import userTypeSelect from '@/components/crm/SelectComponents/UserTypeSelect.vue'

import { getField, getFormField } from '../getField'
import SearchForm from '../components/SearchForm'
import MyTable from '@/components/MyTable'
import {
  getUserTypeList,
  addUserType as addUserTypeApi,
  putUserType as putUserTypeApi
} from '@/api/crmSetting'
import Dialog from '@/components/Dialog'
import MyForm from '@/components/MyForm'
export default {
  data() {
    // 表单配置
    var dialogForm = {
      showFormBorder: false,
      showTitle: false,
      ref: 'customerTableAddForm',
      labelWidth: '100px',
      labelPositon: 'right',
      style: 'notprepend',
      column: 1,
      hasSubmit: true,
      submitDisabled: false,
      submitText: '确认新增',
      cancleText: '取消'
    }
    // 表格操作配置
    var operation = {
      nowPage: 'customerTable',
      show: true,
      fixed: false,
      size: 'mini',
      width: 80,
      minWidth: 100,
      label: '操作',
      btns: [
        {
          type: 'text',
          size: 'mini',
          content: '编辑',
          icon: '',
          handle: 'update',
          class: 'button-operator'
        }
      ]
    }
    return {
      customerTypeValue: '',
      moduleName: '客户类型',
      dialogType: 'status',
      dialogContent: '',
      titleOprator: '新增',
      dialogTitle: '',
      dialogVisible: false,
      dialogForm: dialogForm,
      searchParams: {},
      column: [],
      putVal: {
        is_open: false,
        _id: ''
      },
      formItem: [],
      formData: {},
      tableData: [],
      pageSize: 10,
      currentPage: 1,
      total: 0,
      operation: operation // 操作按钮
    }
  },
  components: {
    MyForm,
    SearchForm,
    Dialog,
    userTypeSelect,
    MyTable
  },
  created() {
    this.fieldInit()
    this.getUserTypeList()
  },
  methods: {
    resetForm() {
      this.customerTypeValue = ''
      this.searchParams = {}
      this.currentPage = 1
      this.getUserTypeList()
    },
    searchFun(val) {
      this.searchParams = val
      this.currentPage = 1
      this.getUserTypeList()
    },
    fieldInit() {
      // 获取字段
      var column = getField('userType')
      column.forEach(item => {
        if (!!item.width && item.width != 'auto') {
          item.width = '300'
        }
      })
      this.column = column
    },
    // 更新数据
    update(row) {
      this.setDialogAttr('编辑', 'update', '确认修改')
      this.dialogFormInit(row)
      this.formItem[0].disabled = true
      this.formItem[2].show = false
      this.dialogShow()
    },
    // 分页切换
    handleCurrentChange(index) {
      this.currentPage = index
      this.getUserTypeList()
    },
    handleSizeChange(val) {
      this.pageSize = val
      this.getUserTypeList()
    },
    setTableData({ list }) {
      this.tableData = list.map((item, index) => ({
        _id: item._id ? item._id : index,
        id: item.id ? item.id : '',
        name: item.name ? item.name : '-',
        desc: item.desc ? item.desc : '-',
        creator: item.creator ? item.creator : '-',
        creatorName: item.creator ? item.creator.name : '-',
        createAt: item.created_at ? this.formatTime(item.created_at) : '-',
        status: item.is_open ? item.is_open : false
      }))
    },
    getUserTypeList() {
      this.handleClose()
      const pageAttr = {
        page_size: this.pageSize,
        current_page: this.currentPage
      }
      const params = Object.assign({}, pageAttr, this.searchParams)
      getUserTypeList(params).then(res => {
        if (res) {
          this.total = res.data.count
          this.setTableData(res.data)
        }
      })
    },
    handleStatusColClick({ row, $index }) {
      this.dialogTitle = '状态切换提示'
      this.dialogType = 'status'
      this.dialogContent = `确认${row.status ? '开启' : '关闭'}该客户类型吗？`
      this.dialogForm.submitText = '状态切换'
      this.putVal.is_open = row.status
      this.putVal._id = row._id
      this.dialogShow()
    },
    dialogFormInit(row) {
      this.formItem = getFormField('userType', 'item')
      this.formData = row ? row : getFormField('userType', 'data')
      this.formItem[0].disabled = false
      this.formItem[2].show = true
    },
    dialogSubmit() {
      this.dialogForm.submitDisabled = true
      if (this.formData._id) {
        this.putUserType(this.formData, '')
        return
      }
      addUserTypeApi(this.formData)
        .then(res => {
          this.dialogForm.submitDisabled = false
          this.$message({
            type: res.data && res.data.result ? 'success' : 'error',
            message:
              res.data && res.data.result
                ? this.dialogForm.submitText + '成功'
                : res.data.message
          })
          if (res.data.result) {
            this.getUserTypeList()
          }
        })
        .catch(error => {
          this.dialogForm.submitDisabled = false
        })
    },
    dialogShow() {
      this.dialogVisible = true
    },
    handleClose() {
      this.dialogVisible = false
    },
    setDialogAttr(oprator, dialogType, submitText) {
      this.titleOprator = oprator
      this.dialogTitle = this.titleOprator + this.moduleName
      this.dialogType = dialogType
      this.dialogForm.submitText = submitText
    },
    addUserType() {
      this.setDialogAttr('新增', 'add', '确认新增')
      const params = {
        name: '',
        desc: ''
      }
      this.dialogFormInit(params)
      this.dialogShow()
    },
    statusConfirm() {
      // 确认修改状态
      this.putUserType(this.putVal, 'status')
    },
    putUserType(val, type) {
      putUserTypeApi(val, type).then(res => {
        this.dialogForm.submitDisabled = false
        this.$message({
          type: res.data && res.data.result ? 'success' : 'error',
          message:
            res.data && res.data.result
              ? this.dialogForm.submitText + '成功'
              : this.dialogForm.submitText + '失败'
        })
        if (res && res.data.result) {
          this.getUserTypeList()
        }
      })
    }
  }
}
</script>
